*,
*::before,
**::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit; 
}

html, body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
  width: 100%;
  margin: 0;
  top: 0;
  overflow: hidden; 
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button {
  display: none;
  margin: 0;
}

.sidepanel {
  width: 4px;
  height: 100%;
  background-color: #434343;
}

.middlewrapper {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.header {
  position: relative;
  top: 0;
  margin: 0;
  height: 2vh;
  width: 100%;
  overflow: hidden;
  background-color: #888888;
}

.footer {
  position: absolute;
  bottom: 0;
  min-height: 12px;
  width: 100%;
  height: 12px;
  z-index: 100;
  background-color: #292929;
}

.wrapper {
  position: absolute;
  height: 100%;
  width: 100%;
  left:0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column; 
}

.canvas {
  display: table;
  margin: 0 auto;
  text-align: center;
}

.wrapper .content_wrapper {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  background-color: #f1f1f1;
}

.wrapper .content_wrapper .content_header {
  background-color: teal;
  height: 2%;
  width: 100%; 
}

.wrapper .content_wrapper .content_footer {
  -ms-flex-item-align: end;
      align-self: flex-end;
  background-color: teal;
  height: 22%;
  width: 100%; 
  margin-bottom: 0px;
}



